import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Input } from '@components/libs/Forms';
import Button from '@components/libs/Button';
import SojiBaseInit from '@modules/SojiBase';
import Cookies from 'js-cookie';
import { Link } from 'react-router-dom';
import Structure from '../../../Structure';
import { useAlertState } from '@components/libs/Notify/useAlertState';
import useUserDetails from '@components/Hooks/useUserDetails'
import styled from 'styled-components';
import '../../index.css'

const SendButton = styled(Button)`
    width: 60%;
    height: 40px;
`;

export default function Login({ pageHandler, embedToGetUserToken }) {
    const useUserDetail = useUserDetails()
    const navigate = useNavigate();
    const alertState = useAlertState();

    const [state, setState] = useState({
        email: '',
        password: '',
        onRequest: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const login = async () => {
        if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
            const SOJIBase = await SojiBaseInit;
            await SOJIBase.deleteCookie();
            useUserDetail.set(false);
        }
        stateHandler('onRequest', true);
        const payload = {
            email: state.email,
            password: state.password,
        };
        const SojiBase = await SojiBaseInit;
        const getter = await SojiBase.post('v1/company/authe/login', payload);
        getter.on('static', (response) => {
            stateHandler('onRequest', false);
            const data = response.response.data;
            if (data.token) {
                Cookies.set('COMPANY_SOJI_uft_token', data.token);
                if (typeof Cookies.get('COMPANY_SOJI_uft_token') != 'undefined') {
                    navigate('/dashboard', { replace: true });
                }
            } else {
                alertState.set({
                    state: true,
                    title: 'Notification',
                    desc: data,
                    type: 'success',
                });
            }
        });
    };

    return (
        <Structure companyAuthe>
            <div className="authe_container">
                <div className="title">Login as a Company</div>
                <Input placeholder="Email" title="Email" onChange={(e) => stateHandler('email', e.target.value)} />
                <Input
                    placeholder="Password"
                    title="Password"
                    type="password"
                    onChange={(e) => stateHandler('password', e.target.value)}
                />
                <div className="authe_action_box">
                    <p>
                        Have no account?
                        <Link to="/company/signup" className="text_default_color cursor_pointer">
                            Sign Up here
                        </Link>
                    </p>
                    <p>
                        <Link to="/company/forgot-password" className="text_default_color cursor_pointer">
                            Forgot Password?
                        </Link>
                    </p>
                </div>
                <div className="button_container">
                    <SendButton title="Login" className="button" loading={state.onRequest} onClick={login} />
                </div>
            </div>
        </Structure>
    );
}
